    "overview": "APITable is an incredibly simple and powerful work management OS. You'll be able to get started in just one second, without any complicated setup or training required.\nOnce you're up and running, you'll find that APITable is the coolest project management software you've ever used. Whether you're a solo entrepreneur or part of a large team, APITable can help you achieve your goals and increase productivity.",
    "websiteurl": null,
    "description": "Customizable View\n7 types of views included.\nAnd you can extend more views by our API & SDK.\n\nTable View, Board View, Gantt View\nArchitecture View, Gallery View, Calendar View\nUse our SDK to extend more views\n\nInfinite Cross Link\nUnlike other Low Code Softwares, Apitable is not a \"Base/App\" structure. You can link unlimited and infinite tables in one space.\n\nLink 1000+ tables together\nOne-direction or Bi-direction Link\nCross Space Shortcuts\n\nRow Permissions\nActivate Rows permissions with a single click through our function called \"Mirror\"\n\nSimple & Powerful row permission\nRows private to only you\nUse a View as an App\n\nOne-Click API Panel\nAPI is the first-class citizen in APITable.\nYou can summon the API power everywhere.\n\nJava/JavaScript/C#/Python... SDK\nCRUD like Database with API\nJSON column, JavaScript column\n\nAutomation\nWith all different kinds of tools connected, you can optimize your workflow solely based on your actual needs.\n\nZapier/Tray.io integration\nScheduled time automation trigger\nExecute code\n\nBI Dashboard\nFreedom \"Dashboard\" interface. You can easily create a BI system.\n\nUse a Dashboard as Standalone App\nCustomize unlimited charts\nDevelop widgets with SDKs",
